<template>
  <div class="discuss-container">
    <div class="message">
      <van-field v-model="value" placeholder="写评论">
        <template slot="left-icon">
          <van-icon name="edit" />
        </template>
      </van-field>
    </div>
    <div class="publish">
      <van-button type="info" size="small" @click="publish">发表</van-button>
    </div>
    <div class="start">
      <van-icon name="star-o" />
    </div>
  </div>
</template>

<script>
import { addComment } from "@/api/comment";
export default {
  data() {
    return {
      value: ""
    };
  },
  methods: {
    async publish() {
      let res = await addComment({
        artid: this.commentid,
        content: this.value,
        commentid: this.artid
      });
      // console.log(res);
      // this.$emit('subCommentVuale', res.new_obj);
      this.value = '';
    }
  },
  props: ["commentid", 'artid'],
  mounted() {
    // console.log('----------------');
    // console.log(this.commentObj);
  },
};
</script>

<style lang="less" scoped>
.discuss-container {
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
  background-color: #fff;
  padding: 5px 0;
  border-top: 1px solid #ccc;

  .message {
    border: 1px solid #aaa;
    border-radius: 15px;
    overflow: hidden;
    flex: 1;
    margin-left: 6px;
  }

  .publish {
    margin: 0 10px;
  }

  .start {
    margin-right: 8px;
  }
}
</style>