<template>
  <div class="detail-container">
    <!-- 头部 -->
    <van-nav-bar title="文章详细" left-arrow @click-left="onClickLeft" fixed />
    <van-cell class="detail-content">
      <!-- 作者信息 -->
      <van-skeleton title avatar :row="3" :loading="isContent">
        <author :artObj="artObj" />
      </van-skeleton>
      <van-divider :style="{ borderColor: '#ccc'}" />

      <!-- 文章内容 -->
      <van-skeleton :row="12" :loading="isContent">
        <div class="content" v-html="artObj.content"></div>
      </van-skeleton>

      <!-- 文章点赞与不喜欢 -->
      <like :artObj="artObj" />
      <van-divider :style="{ color: '#1989fa', borderColor: '#ccc' }">评论</van-divider>

      <!-- 文章评论 -->
      <van-list
        v-model="loading"
        :finished="finished"
        :finished-text="commentList.length === 0 ? '暂时没有评论' : '没有更多了'"
        @load="getCommentsList"
      >
        <div v-for="item in commentList" :key="item.com_id">
          <comment :commentItem="item" />
        </div>
      </van-list>

      <!-- 评论文章 -->
      <discuss :artObj="artObj" @commentVuale="addCommentList" />

      <!-- 评论子回复弹框 -->
      <van-popup
        class="sub-comment"
        v-model="isSubComment"
        round
        position="bottom"
        :style="{ height: '80%' }"
        @closed="initSubComment"
      >
        <comment :commentItem="subComment" />
        <van-divider :style="{ borderColor: '#ccc'}">评论的回复</van-divider>
        <!-- 子评论 -->
        <van-list
          v-model="subLoading"
          :finished="subFinished"
          :finished-text="subCommentList.length === 0 ? '暂时没有评论' : '没有更多了'"
          @load="getSubCommentsList"
        >
          <div v-for="item in subCommentList" :key="item.com_id">
            <comment :commentItem="item" />
          </div>
        </van-list>
        <!--  -->
        <subdiscuss :commentObj="subComment.com_id" :artid="artid" @commentVuale="addCommentList" />
      </van-popup>
    </van-cell>
  </div>
</template>

<script>
// 67573
import eventbus from "@/utils/eventbus";
import author from "@/views/detail/components/author.vue";
import like from "@/views/detail/components/like.vue";
import comment from "@/views/detail/components/comment.vue";
import discuss from "@/views/detail/components/discuss.vue";
import subdiscuss from "@/views/detail/components/subdiscuss.vue";
import { getDetailArticle } from "@/api/article";
import { getComments } from "@/api/comment";
export default {
  data() {
    return {
      isContent: true,
      loading: false,
      finished: false,
      subLoading: false,
      subFinished: false,
      isSubComment: false,
      artid: this.$route.params.artid,
      artObj: "",
      subComment: "",
      limit: 10,
      offset: 0,
      end_id: "",
      subOffset: 0,
      sub_end_id: '',
      commentList: [],
      subCommentList: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    addCommentList(data) {
      this.commentList.unshift(data);
    },
    initSubComment() {
      this.subOffset = 0;
      this.sub_end_id = '';
      this.subFinished = false;
      this.subCommentList = [];
    },
    async getUserInfo() {
      let res = await getDetailArticle(this.$route.params.artid);
      this.artObj = res;
      this.isContent = false;
    },
    async getCommentsList() {
      if (this.offset === 0) {
        let res = await getComments({
          type: "a",
          source: this.$route.params.artid,
          limit: this.limit
        });
        this.offset = res.last_id;
        this.end_id = res.end_id;
        this.commentList = res.results;
      } else {
        let res = await getComments({
          type: "a",
          source: this.$route.params.artid,
          offset: this.offset,
          limit: this.limit
        });
        this.offset = res.last_id;
        this.end_id = res.end_id;
        this.commentList = [...this.commentList, ...res.results];
      }
      this.loading = false;

      if (this.offset === this.end_id) {
        this.finished = true;
      }
    },
    async getSubCommentsList() {
      console.log(this.subComment.com_id);
      if (this.subOffset === 0) {
        let res = await getComments({
          type: "c",
          source: this.subComment.com_id,
          limit: this.limit
        });
        this.subOffset = res.last_id;
        this.sub_end_id = res.end_id;
        this.subCommentList = res.results;
      } else {
        let res = await getComments({
          type: "c",
          source: this.subComment.com_id,
          offset: this.subOffset,
          limit: this.limit
        });
        this.subOffset = res.last_id;
        this.sub_end_id = res.end_id;
        this.subCommentList = [...this.subCommentList, ...res.results];
      }
      this.subLoading = false;

      if (this.subOffset === this.sub_end_id) {
        this.subFinished = true;
      }
    }
  },
  components: {
    author,
    like,
    comment,
    discuss,
    subdiscuss
  },
  created() {
    this.getUserInfo();
    eventbus.$on("subComments", obj => {
      this.isSubComment = obj.isSubComment;
      this.subComment = obj;
    });
  }
};
</script>

<style lang="less" scoped>
.detail-container {
  .detail-content {
    margin: 46px 0;

    /deep/ .content {
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }

    .sub-comment {
      width: 100%;
      padding: 14px 10px 0px;
      box-sizing: border-box;
    }
  }
}
</style>