import request from '@/utils/request';

// 获取频道文章
function getArticle({ channel_id, timestamp, with_top }) {
  return request({
    method: 'GET',
    url: '/v1_1/articles',
    params: {
      channel_id,
      timestamp,
      with_top
    }
  });
}

// 隐藏文章
function hideArticle(id) {
  return request({
    method: 'POST',
    url: '/v1_0/article/dislikes',
    data: {
      target: id
    }
  });
}

// 举报文章
function reportArticle({ target, type }) {
  console.log(target, type);
  return request({
    method: 'POST',
    url: '/v1_0/article/reports',
    data: {
      target: target,
      type: type,
      remark: 'sb'
    }
  })
}

// 获取文章搜索联想
function thinkArticle(keywords) {
  return request({
    method: 'GET',
    url: '/v1_0/suggestion',
    params: {
      q: keywords
    }
  })
}

// 获取搜索文章
function getSearchArticle({ page, per_page, q }) {
  return request({
    method: 'GET',
    url: '/v1_0/search',
    params: {
      page,
      per_page,
      q
    }
  })
}

// 获取文章详细
function getDetailArticle(artid) {
  return request({
    method: 'GET',
    url: `/v1_0/articles/${artid}`,
  })
}

// 不喜欢文章
function dislikeArticle(artid) {
  return request({
    method: 'POST',
    url: '/v1_0/article/dislikes',
    data: {
      target: artid
    }
  })
}

// 取消对文章的不喜欢
function cancelDislikeArticle(artid) {
  return request({
    method: 'DELETE',
    url: `/v1_0/article/dislikes/${artid}`
  })
}

// 点赞文章
function goodArticle(artid) {
  return request({
    method: 'POST',
    url: '/v1_0/article/likings',
    data: {
      target: artid
    }
  })
}

// 取消对文章的点赞
function cancelGoodArticle(artid) {
  return request({
    method: 'DETELE',
    url: `/v1_0/article/likings/${artid}`
  })
}

export {
  getArticle,
  hideArticle,
  reportArticle,
  thinkArticle,
  getSearchArticle,
  getDetailArticle,
  dislikeArticle,
  cancelDislikeArticle,
  goodArticle,
  cancelGoodArticle
}