import request from '@/utils/request';

// 添加评论
function addComment({artid, content, commentid}) {
  return request({
    method: 'POST',
    url: '/v1_0/comments',
    data: {
      target: artid,
      content: content,
      art_id: commentid || null
    }
  })
}

// 获取评论
function getComments({type, source, offset, limit}) {
  return request({
    method: 'GET',
    url: '/v1_0/comments',
    params: {
      type,
      source,
      offset: offset || null,
      limit: limit || null
    }
  })
}

export {
  addComment,
  getComments
}