<template>
  <div class="like-container">
    <van-button type="danger" size="small" icon="star-o" v-if="artObj.attitude === 1" @click="cancelGood">取消点赞</van-button>
    <van-button type="danger" size="small" icon="star-o" plain v-else @click="good">点赞</van-button>
    <van-button type="danger" size="small" icon="like-o" v-if="artObj.attitude === 0" @click="cancelDislike">取消不喜欢</van-button>
    <van-button type="danger" size="small" icon="like-o" plain v-else @click="dislike">不喜欢</van-button>
  </div>
</template>

<script>
import {
  dislikeArticle,
  cancelDislikeArticle,
  goodArticle,
  cancelGoodArticle
} from "@/api/article";
export default {
  data() {
    return {};
  },
  methods: {
    async good() {
      let res = await goodArticle(this.artObj.art_id);
      this.artObj.attitude = 1;
    },
    async cancelGood() {
      let res = await dislikeArticle(this.artObj.art_id);
      this.artObj.attitude = -1;
    },
    async dislike() {
      let res = await dislikeArticle(this.artObj.art_id);
      this.artObj.attitude = 0;
    },
    async cancelDislike() {
      let res = await dislikeArticle(this.artObj.art_id);
      this.artObj.attitude = -1;
    }
  },
  props: ["artObj"]
};
</script>

<style lang="less" scoped>
.like-container {
  display: flex;
  justify-content: space-around;
  margin-top: 25px;
}
</style>