<template>
  <div class="comment-container">
    <van-image width="40" height="40" round :src="commentItem.aut_photo" />
    <van-cell-group class="userIntro" :border="false">
      <!--  -->
      <van-cell :title="commentItem.aut_name" :border="false">
        <template slot="label">
          <div class="content">{{commentItem.content}}</div>
        </template>
      </van-cell>
      <!-- 回复时间和回复 -->
      <van-cell class="time-reply">
        <template slot="title">
          <span>{{commentItem.pubdate | dateFormat1}}</span>
          <span @click="reply">回复({{commentItem.reply_count}})</span>
        </template>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
import eventbus from "@/utils/eventbus";
export default {
  data() {
    return {};
  },
  methods: {
    reply() {
      eventbus.$emit("subComments", {
        isSubComment: true,
        ...this.commentItem
      });
    }
  },
  props: ["commentItem"],
  created() {
    // console.log(this.commentItem);
  },
};
</script>

<style lang="less" scoped>
.comment-container {
  display: flex;
  align-items: flex-start;
  .userIntro {
    flex: 1;

    .content {
      font-size: 14px;
      width: 100%;
    }

    .time-reply {
      padding: 0px 0px 10px 16px;
      span {
        margin-right: 8px;
        font-size: 12px;
      }
    }
  }
}
</style>