<template>
  <div class="author-container">
    <van-image width="60" height="60" round :src="artObj.aut_photo" />
    <van-cell-group class="userIntro" :border="false">
      <van-cell :title="artObj.aut_name">
        <template slot="label">
          <div>{{ artObj.pubdate | dateFormat }}</div>
        </template>
      </van-cell>
    </van-cell-group>
    <van-button size="small" type="danger" plain v-if="artObj.is_followed" @click="follow">关注</van-button>
    <van-button size="small" type="danger" v-else @click="unfollow">已关注</van-button>
  </div>
</template>

<script>
import { followUser, unfollowUser } from "@/api/user.js";
export default {
  data() {
    return {};
  },
  methods: {
    async follow() {
      try {
        await followUser(this.artObj.aut_id);
        this.artObj.is_followed = false;
      } catch (error) {
        console.log(error);
      }
    },
    async unfollow() {
      try {
        await followUser(this.artObj.aut_id);
        this.artObj.is_followed = true;
      } catch (error) {
        console.log(error);
      }
    }
  },
  props: ["artObj"]
};
</script>

<style lang="less" scoped>
.author-container {
  display: flex;
  align-items: center;
  .userIntro {
    flex: 1;
  }
}
</style>